import request from '@/apis/request.wms'

// -
export function getListApi(data) {
  return request({
    url: '/wms/task/myAppliedTask',
    method: 'post',
    data
  })
}

// 
export function getCustomVisitPlanTaskListApi(data) {
  return request({
    url: '/wms/customer/business/plan/approveList',
    method: 'post',
    data
  })
}

//  businessKey taskinfo
export function getTaskInfoByBusinessKeyApi(data) {
  return request({
    url: '/wms/task/main/info/id',
    method: 'post',
    data
  })
}

// 
export function getFlowData(data) {
  return request({
    url: `/flow-manager/app/rest/processIns/json/${data.id}`,
    method: 'get',
    data
  })
}

// 
export function getUserListApi(data) {
  return request({
    url: '/wms/approval/listApprover',
    method: 'post',
    data
  })
}
