<template>
  <el-timeline>
    <el-timeline-item v-for="item in timeLines" :key="item.taskId" :timestamp="item.time" placement="top">
      <el-card class="info">
        <p><span class="blue"> {{ item.userName }} </span>  <span class="warning">{{ item.taskName ? item.taskName : '' }}</span>  <span :class="item.type !== 'BH' ? 'green': 'red'">{{ item.typeName }}</span> </p>
        <p class="message"> {{ item.message }}</p>
      </el-card>
    </el-timeline-item>
  </el-timeline>
</template>

<script>
export default {
  name: 'TaskTimeLine',
  props: {
    timeLines: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data() {
    return {}
  }
}
</script>

<style lang='scss' scoped>
.info {
  span{
    font-weight: 600;
  }
  .blue {
    color: $light-blue;
  }
  .green {
    color: $green;
  }
  .warning {
    color: $warning;
  }
  .red {
    color: $red;
  }
  .message{
    margin-top: 10px;
    font-size: 12px;
  }
}
</style>
