<template>
  <div class="container audit-wrapper">
    <!--  -->
    <div v-if="$route.query.type === 'edit' || $route.query.type === 'audit' || $route.query.type === 'auditHistory' " class="fl time-line">
      <!--  -->
      <div class="active-img">
        <el-button type="success" icon="el-icon-view" @click="searchActiveX"></el-button>
      </div>
      <task-time-line :time-lines="timeLines" class="time-line" />
    </div>
    <!--  -->
    <div class="fr" :style="{ 'width': $route.query.type ? 'calc(100% - 200px)' : '100%' }">
      <slot />
    </div>
    <!--  -->
    <el-dialog
      :key="$route.query.processInstanceId"
      class="flow-dialog"
      title=""
      :close-on-click-modal="false"
      width="1200px"
      append-to-body
      :lock-scroll="false"
      :visible.sync="dialogVisible"
      :before-close="beforeClose"
      top="0"
    >
      <div style="max-height: 90vh; overflow: auto;">
        <div id="container" />
      </div>
    </el-dialog>

    <el-dialog
      title=""
      :close-on-click-modal="false"
      width="900px"
      append-to-body
      :lock-scroll="false"
      :visible.sync="userDialogVisible"
      :before-close="userBeforeClose"
    >
      <el-table ref="table" v-loading="loading" size="mini" :data="list" border stripe style="width: 100%" row-key="id">
        <el-table-column align="center" type="index" label="" />
        <el-table-column prop="username" label="" align="center" />
        <el-table-column prop="no" :label="``" align="center" />
        <el-table-column prop="nickName" :label="``" align="center" />
        <el-table-column prop="companyName" :label="``" align="center" />
        <el-table-column prop="officeName" :label="``" align="center" />
        <el-table-column prop="email" :label="``" align="center" />
        <el-table-column prop="mobile" :label="``" align="center" />
        <el-table-column prop="phone" :label="``" align="center" />
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
import TaskTimeLine from '@/components/AuditWrapper/TaskTimeLine'
import { taskTimeLineMixins } from '@/mixins/task'
import { Graph } from '@antv/x6'
import { getFlowData } from '@/apis/modules/dashboard/task-list.js'
import { getSubmitUserListApi, getUsersByRoleListApi } from '@/apis/modules/user'

export default {
  name: 'AuditWrapperCom',
  components: {
    TaskTimeLine
  },
  mixins: [
    taskTimeLineMixins
  ],
  props: {
    isAudit: {
      type: Boolean,
      default: false
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      dialogVisible: false,
      url: '',
      srcList: [],
      list: [],
      loading: false,
      userDialogVisible: false,
      graph: null,
      flowData: []
    }
  },
  mounted() {
    this.initProcessTimeLine()
    this.createGraph()
  },
  beforeDestroy() {
    if (this.graph) {
      this.graph.dispose()
      this.graph = null
    }
    this.flowData = []
  },
  methods: {
    createGraph() {
      // 
      Graph.registerNode(
        'event',
        {
          inherit: 'circle',
          attrs: {
            body: {
              strokeWidth: 2,
              stroke: '#5F95FF',
              fill: '#FFF'
            },
            text: {
              // refX: 10,
              refY: 40
            }
          }
        },
        true,
      )
      Graph.registerNode(
        'startEvent',
        {
          inherit: 'circle',
          attrs: {
            body: {
              strokeWidth: 2,
              stroke: 'rgb(88, 88, 88)',
              fill: '#FFF'
            },
            text: {
              // refX: 10,
              refY: 30
            }
          }
        },
        true,
      )

      Graph.registerNode(
        'endEvent',
        {
          inherit: 'circle',
          attrs: {
            body: {
              strokeWidth: 3,
              stroke: 'rgb(88, 88, 88)',
              fill: '#FFF'
            },
            text: {
              // refX: 10,
              refY: 30
            }
          }
        },
        true,
      )

      // 
      Graph.registerNode(
        'activity',
        {
          inherit: 'rect',
          markup: [
            {
              tagName: 'rect',
              selector: 'body'
            },
            // {
            //   tagName: 'image',
            //   selector: 'img'
            // },
            {
              tagName: 'text',
              selector: 'label'
            }
          ],
          attrs: {
            body: {
              rx: 6,
              ry: 6,
              stroke: 'rgb(88, 88, 88)',
              fill: '#EFF4FF',
              strokeWidth: 1
              // event: 'node:showUser'
            },
            // img: {
            //   x: 6,
            //   y: 6,
            //   width: 16,
            //   height: 16,
            //   'xlink:href': 'https://gw.alipayobjects.com/mdn/rms_43231b/afts/img/A*pwLpRr7QPGwAAAAAAAAAAAAAARQnAQ',
            //   event: 'node:showUser'
            // },
            label: {
              fontSize: 10,
              fill: '#262626'
            }
          }
        },
        true,
      )
      // 
      Graph.registerNode(
        'cur-activity',
        {
          inherit: 'rect',
          markup: [
            {
              tagName: 'rect',
              selector: 'body'
            },
            // {
            //   tagName: 'image',
            //   selector: 'img'
            // },
            {
              tagName: 'text',
              selector: 'label'
            }
          ],
          attrs: {
            body: {
              rx: 6,
              ry: 6,
              stroke: '#f00',
              fill: '#EFF4FF',
              strokeWidth: 3
              // event: 'node:showUser'
            },
            // img: {
            //   x: 6,
            //   y: 6,
            //   width: 16,
            //   height: 16,
            //   'xlink:href': 'https://gw.alipayobjects.com/mdn/rms_43231b/afts/img/A*pwLpRr7QPGwAAAAAAAAAAAAAARQnAQ',
            //   event: 'node:showUser'
            // },
            text: {
              fontSize: 10,
              fill: '#262626'
            }
          }
        },
        true,
      )

      // 
      Graph.registerNode(
        'gateway',
        {
          inherit: 'polygon',
          attrs: {
            body: {
              refPoints: '0,10 10,0 20,10 10,20',
              strokeWidth: 2,
              stroke: 'rgb(88, 88, 88)',
              fill: '#EFF4FF'
            },
            label: {
              text: '+',
              fontSize: 40,
              fill: 'rgb(88, 88, 88)',
              refY: 15
            }
          }
        },
        true,
      )

      // 
      Graph.registerEdge(
        'bpmn-edge',
        {
          inherit: 'edge',
          attrs: {
            line: {
              stroke: 'rgb(88, 88, 88)',
              strokeWidth: 2
            }
          },
          label: {
            attrs: {
              label: {
                fill: 'rgb(88, 88, 88)',
                fontSize: 12
                // refY: -10
                // refX: 10
              }
            },
            position: {
              distance: 0.3
            }
          }
        },
        true,
      )

      // 
      Graph.registerEdge(
        'his-bpmn-edge',
        {
          inherit: 'edge',
          attrs: {
            line: {
              stroke: '#f00',
              strokeWidth: 2
            }
          },
          label: {
            attrs: {
              label: {
                fill: '#f00',
                fontSize: 12
                // refY: -10
              }
            },
            position: {
              // distance: 15
            }
          }
        },
        true,
      )

      // 
      Graph.registerEdge(
        'lane-edge',
        {
          inherit: 'edge',
          attrs: {
            targetMarker: '',
            line: {
              stroke: '#A2B1C3',
              strokeWidth: 2,
              targetMarker: null
            }
          },
          label: {
            attrs: {
              label: {
                fill: '#A2B1C3',
                fontSize: 12,
                refY: -10
              }
            }
          }
        },
        true,
      )
    },
    // 
    searchActiveX() {
      this.dialogVisible = true
      const _this = this
      getFlowData({
        id: this.$route.query.processInstanceId
      }).then(res => {
        const { code, data } = res
        if (!code) {
          this.flowData = []
          let w = 1200
          let h = 800
          data.forEach(item => {
            const { vertices, shape, position, width, height, ...obj } = item
            if (position && h < position.y + height) {
              h = Math.round(position.y + height)
            }
            if (position && w < position.x + width) {
              w = Math.round(position.x + width)
            }
            if (shape !== 'bpmn-edge') {
              obj.position = {
                x: Math.floor(position.x + 0.2 * width),
                y: Math.floor(position.y + 0.2 * height)
              }
              obj.width = Math.round(width * 0.6)
              obj.height = Math.round(height * 0.6)
              // 
              if (obj.label && obj.label.length > 8) {
                obj.label = obj.label.substring(0, 8) + '\n' + obj.label.substring(8)
              }
              // 
              if (obj.active && shape === 'activity') {
                obj.shape = 'cur-activity'
              }
              this.flowData.push({ shape, ...obj })
            } else {
              vertices.pop()
              vertices.shift()
              if (obj.history) {
                obj.shape = 'his-bpmn-edge'
              }
              this.flowData.push({ shape, vertices, ...obj })
            }
          })
          this.graph && this.graph.dispose()
          //  Graph 
          this.graph = new Graph({
            container: document.getElementById('container'),
            height: h, // 
            width: w, // 
            connecting: {
              router: 'orth'
            },
            scaling: {
              min: 1,
              max: 1.5
            },
            grid: true, // 
            interacting: false
          })

          this.graph.on('node:click', ({ cell }) => {
            if (cell.shape.indexOf('cur-activity') > -1) {
              const { ename1 } = this.extraInfo
              if (!ename1) return
              // 
              _this.userDialogVisible = true
              _this.getUsersByRoleList({ ename: ename1 })
            }
            // if (cell.shape.indexOf('activity') === -1) {
            //   return
            // }
            // const { submitter } = this.extraInfo
            // const obj = {
            //   businessId: this.$route.params.id,
            //   processInstanceId: this.$route.query.processInstanceId,
            //   node: cell.id
            // }
            // 
            // _this.userDialogVisible = true
            // _this.getUserList({ ids: [submitter] })
          })

          const cells = []
          this.$nextTick(() => {
            this.flowData.forEach((item) => {
              if (item.shape.indexOf('bpmn-edge') > -1) {
                cells.push(this.graph.createEdge(item))
              } else {
                cells.push(this.graph.createNode(item))
              }
            })

            this.graph.resetCells(cells)
            this.graph.zoomToFit({ padding: 10, maxScale: 1.2 })
          })
        }
      })
    },
    getUsersByRoleList(obj) {
      this.loading = true
      // Tip 
      getUsersByRoleListApi({
        ...obj
      }).then(res => {
        const { code, data } = res
        if (!code) {
          // this.list = data.map(item => {
          //   const { company, office, ...info } = item
          //   return {
          //     ...info,
          //     companyName: company.name,
          //     officeName: office.name
          //   }
          // })
          this.list = data.map(item => {
            const { company, office, ...info } = item
            return {
              ...info,
              companyName: company && company.name || '',
              officeName: office && office.name || ''
            }
          })
        }
        this.loading = false
      }).catch(err => {
        this.loading = false
        console.log('err', err)
      })
    },
    getUserList(obj) {
      this.loading = true
      // Tip 
      getSubmitUserListApi({
        ...obj
      }).then(res => {
        const { code, data: { list }} = res
        if (!code) {
          // this.list = data.map(item => {
          //   const { company, office, ...info } = item
          //   return {
          //     ...info,
          //     companyName: company.name,
          //     officeName: office.name
          //   }
          // })
          this.list = list.map(item => {
            const { company, office, ...info } = item
            return {
              ...info,
              companyName: company && company.name || '',
              officeName: office && office.name || ''
            }
          })
        }
        this.loading = false
      }).catch(err => {
        this.loading = false
        console.log('err', err)
      })
    },
    beforeClose() {
      this.dialogVisible = false
      this.graph.dispose()
      this.graph = null
    },
    userBeforeClose() {
      this.userDialogVisible = false
    }
  }
}
</script>

<style lang='scss' scoped>
.container{
  width: 100%;
  min-height: calc(100vh - 90px - 40px);
  .time-line{
    width: 180px;
    margin-right: 20px;
  }
  .dashboard{
    width: calc(100vw - 210px - 200px - 80px);
    .form-wrapper{
      font-size: 14px;
      line-height: 30px;
      .title {
        width: 110px;
        font-size: 12px;
        color: #99a9bf;
      }
      .value{
        color: #606266;
      }
    }
  }
}
.audit-wrapper {
  display: flex;
  flex: 1;
}
.active-img {
  display: flex;
  justify-content: center;
  margin: 10px 0;
}

.flow-dialog {
  display: flex;
  justify-content: center;
  align-items: center;
  ::v-deep .el-dialog{
    margin: 0 auto;
    .el-dialog__body {
      padding: 0;
    }
  }

}
</style>
