import { getProcessTimeLineApi } from '@/apis/modules/common'

// 
export const taskTimeLineMixins = {
  data() {
    return {
      timeLines: []
    }
  },
  methods: {
    // 
    initProcessTimeLine() {
      console.log(this.$route.query)
      const processInstanceId = this.$route.query.processInstanceId
      if (!processInstanceId) return
      getProcessTimeLineApi({
        processInstanceId
      }).then(res => {
        const { code, data } = res
        if (!code) {
          this.timeLines = data
        }
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
